.container {
  width: 40vw;
  @media screen and (max-width: 768px) {
    width: 76px;
  }
  font-size: 18px;
  @media screen and (max-width: 768px) {
    font-size: 24px;
  }
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 12vh 5vw;
}
