$text: #000000; // 주석
$back: white;
* {
  margin: 0px;
  color: $text;
}

.main_img {
  font-size: 18px;
  height: 30vh;
  margin-top: 12vh;
  @media screen and (max-width: 768px) {
    font-size: 390px;
  }
}
.main_sub {
  width: 100%;
  height: 5vh;
  line-height: 5vh;
  vertical-align: center;
  text-align: center;
  font-size: 18px;
  font-weight: 700;
  @media screen and (max-width: 768px) {
    font-size: 380px;
  }
}
